<template>

    <div class="title-root">

        <div class="back" v-if="back" @click="clickBack">
            <icon name="angle-left" scale="1.2"></icon>
            &nbsp;
            <span>返回</span>
        </div>
        <div class="title">{{title}}</div>

    </div>

</template>

<script>
    import Icon from "vue-awesome/components/Icon";

    export default {
        name: "TitleBar",
        components: {Icon},
        props: {
            back: {
                require: false,
                type: Boolean,
                default: false
            },
            title: {
                require: false,
                type: String,
                default: ""
            }
        },
        methods: {
            clickBack() {
                this.$emit("onBack")
            }
        }
    }
</script>

<style scoped>

    .title-root {
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        position: relative;
        width: 100%;
        height: 5rem;
        border: .1rem none #33333333;
        border-bottom-style: solid;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .back {
        position: absolute;
        left: 0;
        top: 0;
        width: 5rem;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.2rem;
    }

    .back:active {
        background-color: #2b81af;
        color: white;
        transition: all .4s;
    }

    .title {
        font-size: 1.8rem;
    }

</style>