<template>
    <div class="root">

        <title-bar :title="'登录'" :back="true" @onBack="clickBack"></title-bar>

        <div class="content">

            <div class="content-container">
                <div class="input-item">
                    <input class="input" v-model="email" :placeholder="'请输入邮箱'"/>
                </div>
                <div class="input-item">
                    <input class="input" v-model="password" :placeholder="'请输入密码'" type="password"/>
                </div>

                <div class="input-item">
                    <div class="btn-login" @click="clickLogin">登录</div>
                </div>

                <div class="footer">
                    <router-link :to="{name:'userReset'}"
                                 class="footer-item">忘记密码
                    </router-link>
                    |
                    <router-link :to="{name:'userRegister'}"
                                 class="footer-item">去注册
                    </router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import TitleBar from "../components/TitleBar";
    import {mapActions, mapGetters} from 'vuex'

    export default {
        name: "UserLoginPage",
        components: {TitleBar},
        data() {
            return {
                email: "",
                password: ""
            }
        },
        computed: {
            ...mapGetters({
                isLogin: 'isLogin'
            })
        },
        watch: {
            isLogin(newVal, oldVal) {
                if (newVal) {
                    this.$router.push({path: '/me'})
                }
            }
        },
        methods: {
            ...mapActions({
                login: 'login'
            }),
            clickBack() {
                this.$router.push({name: 'home'})
            },
            clickLogin() {
                const email = this.email
                const password = this.password
                this.login({email, password})
            }
        }
    }
</script>

<style scoped>

    .content-container {
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .input-item {
        width: 80%;
        margin-top: 1.5rem;
    }

    .input {
        width: 100%;
        height: 2.5rem;
        text-align: center;
    }

    .btn-login {
        width: 100%;
        height: 3rem;
        border: .1rem solid #33333333;
        border-radius: .3rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .btn-login:active {
        background-color: #33333333;
        transition: all .3s;
    }

    .footer {
        width: 80%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 5rem;
    }

    .footer-item {
        width: 6rem;
        height: 2rem;
        font-size: 1.2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        text-decoration: none;
        color: black;
    }

</style>